var dateHelper = require('../helper/date-helper.js');
var Demo = React.createClass({
    render: function() {
        return (
                <div>
                    <p>
                        Hello, world!I am a React App.
                    </p>
                    <p>
                        {dateHelper.getShortDatetime()}
                    </p>
                </div>
            );
    }
});

ReactDOM.render(<Demo />, document.getElementById('app'));


